import Grid from '@material-ui/core/Grid';
import Tooltip from '@material-ui/core/Tooltip'
import Link from 'next/link'
import home from '../icon/home.svg'


const Header = () => {
    return (
        <dir id="bg">
            <Grid container spacing={24}>
                <Grid item xs={2}>
                    <div >
                        <Tooltip title="Home" placement="right">
                            <Link href="/">
                                <img src={home} alt="Elderly" width="50" /></Link>
                        </Tooltip>
                    </div>
                </Grid>

                <Grid item xs={8}>
                    <div >
                        <ul class="nav">
                                <a class="nav-link active" href="/D"><h5 id="frontbar" className="fornt">หน่วยงาน</h5></a>
                                <a class="nav-link active" href="/D"><h5 id="frontbar" className="fornt">เรื่องน่ารู้</h5></a>
                                <a class="nav-link active" href="/D"><h5 id="frontbar" className="fornt">ติดต่อเรา</h5></a>
                           
                        </ul>
                    </div>
                </Grid>

                <Grid item xs={2}>
                    <div>
                        <h5 id="frontbarL">สายด่วน โทร. 1300</h5>
                    </div>
                </Grid>

            </Grid>

        </dir>
    )
}
export default Header